@font-face {
  font-family: Avenir;
  src: url("https://s3.eu-west-2.amazonaws.com/emperia.digital/Bloomingdales/Technical-Preview/static/bloomingdales/static/media/AvenirLTStd-Light.4343d6ec50614bf0b4a4.otf");
}

@font-face {
  font-family: AvenirMedium;
  src: url("https://s3.eu-west-2.amazonaws.com/emperia.digital/Bloomingdales/Technical-Preview/static/bloomingdales/static/media/AvenirLTStd-Medium.adcbfbb892596d80fae1.otf");
}

@font-face {
  font-family: AvenirHeavy;
  src: url("https://s3.eu-west-2.amazonaws.com/emperia.digital/Bloomingdales/Technical-Preview/static/bloomingdales/static/media/AvenirLTStd-Heavy.d1a180cf4bf435707e5d.otf");
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: transparent;
  box-sizing: border-box;
  max-height: 100vh;
  overflow-y: hidden !important;
}

.main {
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100vh;
  max-height: 100vh;
  overflow-y: hidden !important;
}

.mobile-only {
  display: none !important;
}

.brand-name {
  letter-spacing: 12%;
}

.modal-container {
  height: 700px;
}
.modal-content {
  flex-direction: row;
}

.ol-carousel {
  width: 50%;
  height: fit-content;
}
.ol-carousel-btns {
  display: none !important;
  margin-top: 308px !important;
  max-width: 576px;
}
.ol-carousel-expand-btn {
  display: none !important;
  margin: -4px 0 0 4px;
}
.hidden-clickable {
  display: none;
  justify-content: center;
  user-select: none;
  cursor: pointer;
}
.ol-carousel:hover>.ol-carousel-btns, .ol-carousel:hover>.ol-carousel-expand-btn {
  display: flex !important;
}
.ol-carousel-indicators {
  max-width: 90%;
  max-height: 3px;
  margin: 16px auto;
}
.ol-carousel-indicator {
  background-color: #F1F1F1 !important;
}
.ol-carousel-indicator-selected {
  background-color: #6E6E6E !important;
}

.image-expand-button {
  bottom: 0;
}
.image-left-button {
  left:0;
  bottom:50%;
}
.image-right-button {
  right:50%;
  bottom:50%;
}
.checkout-view {
  width: 50%;
  padding: 64px;
}
.product-details {
  padding: 64px 64px 0 64px;
}
.details-view {
  height: 100%;
}

.clickable {
  display: flex;
  justify-content: center;
  user-select: none;
  cursor: pointer;
}

.menu-svg {
  width: 13px;
  height: 13px;
  margin: auto 0;
}

.size-select-boxes {
  display: flex;
  flex-direction: row;
  align-items: left;
  justify-content: left;
}

.size-select-boxes>div{
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;

  width: 48px;
  height: 48px;
  border: 1px solid #949494;
  margin: 0 6px 0 6px;
  padding: 0 0 0 0;

  user-select: none;
  cursor: pointer;
}

.out-of-stock {
  position: absolute;
  width: 68px;
  margin-top: -48px;
  margin-left: 20px;
  border-bottom: 1px solid #949494;
  transform: rotate(45deg);
  transform-origin: top left;
}

@media screen and (max-width: 1152px) {
  .desktop-only {
    display: none !important;
  }
  .mobile-only {
    display: flex !important;
  }
  .details-view {
    height: calc(100% - 128px) !important;
    padding: 64px 32px !important;
  }
  .modal-container {
    height: 100% !important;
    max-height: 90vh !important;
    width: 600px !important;
    overflow-x: hidden;
    overflow-y: hidden;
    margin: unset !important;
    padding: unset !important;
  }
  .modal-content {
    flex-direction: column;
    max-height: 95vh;
    overflow-y: scroll;
  }
  .image-container {
    width: 100%;
    min-height: 80vh !important;
    background-color: white !important;
  }
  .product-image {
    width: 538px;
    height: 538px;
    min-height: 80vh !important;
    margin: 0 auto;
  }
  .image-expand-button {
    display: flex !important;
    position: relative !important;
    top: unset !important;
    left: unset !important;
    right: unset !important;
    bottom: unset !important;
    margin-top: -39px;
  }
  .showClickableOnHover:hover>.image-expand-button {
    margin-top: 218px !important;
  }
  .image-left-button {
    position: relative !important;
    top: unset;
    left: unset;
    right: unset;
    bottom: unset;
    margin-top: -289px;
    margin-right: auto;
  }
  .image-right-button {
    position: relative !important;
    top: unset;
    left: unset;
    right: unset;
    bottom: unset;
    margin-top: -48px;
    margin-left: auto;
  }
  .add-to-bag-button {
    z-index: 3;
    position: absolute !important;
    bottom: 0;
    left: 0;
    min-width: 100%;
    padding: 40px 64px 40px 64px !important;
    background-color: white !important;
    box-shadow: 0px 0px 8px 10px rgba(0, 0, 0, 0.05);
  }
  .product-value {
    z-index: 0 !important;
  }
  .product-details {
    min-width: 100% !important;
    margin-bottom: 248px !important;
    padding: 0 64px 0 64px !important;
  }
  .checkout-view {
    width: 80%;
  }
  .ol-carousel {
    z-index: 3;
    width: 600px;
    min-height: 768px;
    overflow-y: hidden !important;
    overflow-x: hidden;
    background-color: white;
  }
  .ol-carousel-image {
    max-height: calc(100% - 64px);
  }
  .ol-carousel-btns {
    display: none !important;
    min-width: 100% !important;
    pointer-events: none;
  }
}

@media screen and (max-width: 600px) {
  .ol-carousel {
    width: 100% !important;
    max-width: 425px;
    margin: 0 auto;
    height: 100% !important;
    min-height: 645px;
    max-height: 645px;
    overflow-y: visible;
  }
  .ol-carousel-image {
    min-width: 375px !important;
    max-height: calc(100% - 64px);
  }
  .ol-carousel-item {
    height: 100%;
  }
  .ol-carousel-btns {
    display: none !important;
    width: 100vw !important;
    margin-top: 186px !important;
  }
  .modal-container {
    height: 90vh !important;
    width: 100vw !important;
    max-width: 500px !important;
    padding: unset !important;
    margin: unset !important;
  }
  .modal-content {
    flex-direction: column;
    max-height: 100%;
    overflow-y: scroll;
    padding-bottom: 5vmin;
  }
  .product-value {
    padding: 64px 64px 0px 0;
  }
  .product-details {
    min-width: 100% !important;
    margin-bottom: 248px !important;
    padding: 0 24px 0 24px !important;
  }
  .add-to-bag-button {
    padding: 32px !important;
  }
  .checkout-view {
    padding: unset;
  }
  .details-view {
    height: calc(100% - 128px) !important;
  }
}

::-webkit-scrollbar {
  width: 0px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

::-webkit-scrollbar-thumb {
  background-color: rgb(202, 202, 202);
}
